/* particles.css -- Styles for heading banners */

#particles-js {
  background-color: rgb(19, 21, 22);
  width: 100%;
  height: 200px;
  z-index: -1;
  position: relative;
}

#particles-js-resume {
  background-color: rgb(35, 39, 65);
  width: 100%;
  height: 200px;
  z-index: -1;
  position: relative;
}

#particles-js-portfolio {
  background-color: rgb(114,0,0);
  width: 100%;
  height: 200px;
  z-index: -1;
  position: relative;
}

p.bannerText {
  position: absolute;
  z-index: 100;
  color: whitesmoke;
  background-color: transparent;
  text-align: center;
  font-family: "Crimson Text", Arial, Helvetica, sans-serif;
  font-size: 4em;
  width: 100%;
  height: 200px;
  line-height: 200px;
}
